import React, { Dispatch } from 'react';

interface Store {
  email: [string, Dispatch<string>];
  telephone: [string, Dispatch<string>];
  comment: [string, Dispatch<string>];
  productIds: [string[], Dispatch<string[]>];
  serialisedItemIds: [string[], Dispatch<string[]>];
  addProductId: (v: string) => void;
  removeProductId: (v: string) => void;
  addSerialisedItemId: (v: string) => void;
  removeSerialisedItemId: (v: string) => void;
}

export const StoreContext = React.createContext<Store>({} as Store);

export const StoreProvider: React.FunctionComponent = ({ children }) => {
  // Request
  const [email, setEmail] = React.useState(null);
  const [telephone, setTelephone] = React.useState(null);
  const [comment, setComment] = React.useState(null);
  const [productIds, setProductIds] = React.useState([]);
  const [serialisedItemIds, setSerialisedItemIds] = React.useState([]);

  const remove = (list: string[], value: string) => list.filter((v) => v !== value);

  const store: Store = {
    email: [email, setEmail],
    telephone: [telephone, setTelephone],
    comment: [comment, setComment],
    productIds: [productIds, setProductIds],
    serialisedItemIds: [serialisedItemIds, setSerialisedItemIds],
    addProductId: (value: string) => {
      setProductIds([...remove(serialisedItemIds, value), value]);
    },
    removeProductId: (value: string) => {
      setProductIds(remove(productIds, value));
    },
    addSerialisedItemId: (value: string) => {
      setSerialisedItemIds([...remove(serialisedItemIds, value), value]);
    },
    removeSerialisedItemId: (value: string) => {
      setSerialisedItemIds(remove(serialisedItemIds, value));
    },
  };

  return <StoreContext.Provider value={store}>{children}</StoreContext.Provider>;
};
