import React, { Dispatch } from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import { Query } from '../../graphql-types';

interface Search {
  term: [string, Dispatch<string>];
  options: { slug: string; label: string }[];
}

export const SearchContext = React.createContext<Search>({} as Search);

export const SearchProvider: React.FunctionComponent = ({ children }) => {
  const [term, setTerm] = React.useState(null);

  const { allSitePage } = useStaticQuery<Query>(
    graphql`
      query {
        allSitePage(
          filter: { context: { language: { eq: "fr" } }, componentPath: { regex: "/(brand.tsx|familiy.tsx|group.tsx|product.tsx)/" } }
        ) {
          nodes {
            context {
              slug
            }
          }
        }
      }
    `,
  );

  const options = allSitePage.nodes.map((v) => {
    const slug = v.context.slug;
    return { slug, label: slug.replace('-', ' ') };
  });

  options.sort((a, b) => a.label.localeCompare(b.label));

  const search: Search = {
    term: [term, setTerm],
    options,
  };

  return <SearchContext.Provider value={search}>{children}</SearchContext.Provider>;
};
